* {
    box-sizing: border-box;
}

main {
    padding: 36px;
    background-color: none;
    height: 100vh;
}
.form {
    display: grid;
    grid-template: 40px 40px / 1fr 1fr ;
    gap: 17px;
    margin-bottom: 17px;
}

.form--input{
    border: none;
    border-radius: 5px;
    text-indent: 10px;
}

.form--btn {
    border: 1px solid #D5D4D8;
    grid-column: 1 / -1;
    border-radius: 5px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background: linear-gradient(to right, #2c3e50, #2980b9);
    color: white;
    cursor: pointer;
    width: 50%;
    height: 100%;
    margin: auto;
}

.meme {
    position: relative;
    flex: row;
    text-align: center;
}

.meme--image {
    max-width: 100%;
    height: 100%;
    border-radius: 3px;
}


.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}