.header {
    display: flex;
    align-items: center;

    height: 12vh;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

    font-size: 1rem;
    color: white;


    padding: 15px;
    background: linear-gradient(to right, #2c3e50, #2980b9); 
}

.header .header--image {
    height: 130%;
    margin-right: 15px;
    object-fit: contain;
}

.header .header--title {
    font-size: 2rem;
    margin-right: auto;
    font-weight: 400;
}
